body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

.About {
  color: #293347;
  position: relative;
  min-height: 100%;
  max-width: 100%;
  overflow-x: hidden;
  margin-top: 0;
  margin-bottom: 80px;
  text-align: center;
  padding-top: 60px;
}
.head {
  font-weight: 600;
}

#paddings {
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 10px;
}
#top {
  padding-top: 16px;
  font-weight: 800;
}

#bottom {
  margin-bottom: 10px;
}

#skills {
  margin-top: 20px;
  min-height: 100%;
  text-align: center;
}

#borderRadius {
  border-radius: 15px;
}

.padding-10 {
  margin-right: 5px;
}
@media (max-width: 770px) {
  .buttonpad {
    margin-bottom: 20px;
  }
}

.stickyfooter {
  width: 100%;
  margin-top: 50px;
}

#footerlinks {
  color: white;
  text-decoration: none;
}

#spacing {
  padding: 60px 0;
}

.fa {
  padding: 13px;
  font-size: 25px;
  width: 50px;
  height: 50px;
  text-align: center;
  text-decoration: none;
  margin: 3px 12px;
}

.fa:hover {
  opacity: 0.7;
}

.fa-twitter {
  background: #55acee;
  color: white;
  border-radius: 50%;
}
.fa-github {
  background: white;
  color: black;
  border-radius: 50%;
}
.fa-google {
  background: #dd4b39;
  color: white;
  border-radius: 50%;
}
.fa-linkedin {
  background: #007bb5;
  color: white;
  border-radius: 50%;
}
.fa-dribbble {
  background: #ea4c89;
  color: white;
  border-radius: 50%;
}

#textdecoration {
  text-decoration: none;
  color: white;
}
@media (max-width: 772px) {
  #spacingTop {
    padding-top: 50px;
  }
}

.Portfolio {
  color: #293347;
  position: relative;
  overflow: scroll;
  min-height: 100vh;
  margin-top: 0;
  text-align: justify;
  overflow: scroll;
  padding-top: 20px;
}

.imgcontainer {
  position: relative;
  width: 50%;
}

.image {
  display: block;
  width: 100%;
  min-height: 250px;
  height: auto;
}

.overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #f5f5f5;
  overflow: hidden;
  width: 100%;
  height: 100%;
  -webkit-transform: scale(0);
  transform: scale(0);
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
}

.imgcontainer:hover .overlay {
  -webkit-transform: scale(1);
  transform: scale(1);
}

.imgtext {
  color: #293347;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}
.visitbutton {
  margin-top: 10px;
  border: 1.3px solid #e31b6d;
  font-family: "Raleway", sans-serif;
  background: inherit;
  font-size: 12pt;
  font-weight: 100;
  color: #1b242f;
  width: 170px;
  line-height: 22pt;
  text-align: center;
}
.visitbutton:hover {
  background: #e31b6d;
  color: white;
  font-family: "Raleway", sans-serif;
}
.fontRaleway {
  font-family: "Raleway", sans-serif;
  color: #e31b6d;
  font-size: 18px;
  margin-top: -15px;
}
.Rbold {
  font-family: "Raleway-bold", sans-serif;
  color: #1b242f;
}
.badgeStyle {
  font-family: "Raleway", sans-serif;
  font-weight: "100";
  background: #e31b6d;
  color: white;
  margin: 20px 20px;
  line-height: 60px;
  padding: 4px 13px;
}
.badgeStyle:hover {
  text-decoration: none;
  background: #e31b6d;
  color: white;
}

.heading {
  text-decoration: none;
  color: #293347;
  text-align: left;
}
.imgShowcase {
  width: 100%;
  height: 160px;
  padding: 0;
}
.my-container {
  color: #293347;
}

@media (max-width: 1000px) {
  .imgShowcase {
    width: 100%;
    height: auto;
    padding-bottom: 20px;
  }
}
@media (max-width: 772px) {
  .imgShowcase {
    padding-bottom: 10px;
  }
}

.Blog {
  color: #293347;
  position: relative;
  overflow: scroll;
  min-height: 120vh;
  margin-top: 0;
  text-align: justify;
  overflow: scroll;
  padding-top: 50px;
}

#title {
  padding: 16px 16px 0 16px;
}
#subhead {
  padding-left: 16px;
  padding-right: 16px;
}

.main-content {
  height: 210px;
  width: 80%;
  border: 1px solid #eee;
  border-radius: 5px;
}
.main-content img {
  float: right;
  margin: 0 0 20px 20px;
  height: 200px;
  width: 300px;
  opacity: 1;
  opacity: initial;
}
@media (max-width: 576px) {
  .main-content img {
    -webkit-align-content: center;
            align-content: center;
    margin-left: 0;
    float: none;
    height: auto;
    width: 100%;
    opacity: 1;
    opacity: initial;
  }
}

.FullArticle {
  color: #293347;
  position: relative;
  overflow: scroll;
  min-height: 120vh;
  margin-top: 0;
  overflow: scroll;
  padding-top: 50px;
  width: 60%;
}
.articlehead {
  font-weight: 400;
  margin-top: 25px;
}
.articleText {
  font-size: 18px;
}
.responsiveImg {
  height: 400px;
  width: 100%;
  margin-top: 20px;
  margin-bottom: 30px;
}
@media (max-width: 990px) {
  .FullArticle {
    width: 95%;
  }
  .responsiveImg {
    height: auto;
    width: 100%;
    margin-top: 20px;
    margin-bottom: 30px;
  }
}
@media (max-width: 572px) {
  .articleText {
    font-size: 16px;
  }
}
.subhead {
  margin: 15px 0;
}

.highlight {
  background: #f5f2f0;
  padding: 10px 10px;
  font-size: 18px;
  margin: 10px 10px;
}
.highlightkeywords {
  background: #f5f2f0;
  padding: 0 2px;
  margin: 0 3px;
  border-radius: 4px;
}

.fiddlestyle {
  margin: 15px 0;
}

.imgCenter {
  text-align: center;
}

@media (max-width: 600px) {
  .imgstyle {
    text-align: center;
    margin: 20px 0;
    width: 100%;
    height: auto;
  }
  .imgCenter {
    text-align: center;
  }
}
.weight {
  font-weight: 900;
  font-size: 20px;
}


